<template>
  <div>
    <div class="filter-container">
      <el-select style="width: 120px" v-model="queryType" slot="prepend" class="filter-item">
        <el-option v-for="(item,index) in chooseList"
                   :key="index"
                   :value="item.value"
                   :label="item.label"></el-option>
      </el-select>
      <el-input @keyup.enter.native="handleQuery" style="width: 200px;" class="filter-item" :placeholder="chooseName" v-model="queryValue">
      </el-input>
      <el-button class="filter-item" type="primary" icon="search" @click="getList">搜索</el-button>
      <el-button class="filter-item" style="margin-left: 10px;"  type="primary" icon="edit" @click="handleadd">添加</el-button>
    </div>
    <div style="margin-top: 20px;">
      <el-table ref="multipleTable"
                :data="list"
                tooltip-effect="dark"
                border fit highlight-current-row style="width: 100%;"
                height="600"
                row-key="id">
        <el-table-column width="65" align="center" label="序号" prop="index"></el-table-column>
        <el-table-column width="center" align="center" label="imei号" prop="device_imei"></el-table-column>
        <el-table-column width="center" align="center" label="rfid设备号" prop="device_rfid"></el-table-column>
        <el-table-column align="center" label="操作" width="150">
          <template slot-scope="scope">
            <el-button size="small" type="primary" @click="handleUpdate(scope.row)">修改
            </el-button>
            <el-button size="small" type="danger" @click="handleDelete(scope.row)">删除
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div>
      <div class="pagination-container">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="listQuery.page"
          :page-sizes="pageSizes"
          :page-size="listQuery.limit"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
        </el-pagination>
        <!--分页展示-->
      </div>
    </div>
    <el-dialog :title="textMap[dialogStatus]" :visible.sync="dialogFormVisible">
      <el-form class="small-space" :model="temp" ref="temp" label-position="left" label-width="100px" style='max-width: 400px; margin-left:50px;'>
        <el-form-item label="imei" prop='name'>
          <el-input v-if="dialogStatus=='create'" v-model="temp.imei"></el-input>
          <el-input v-else v-model="temp.imei"></el-input>
        </el-form-item>
        <el-form-item label="rfid" prop='name'>
          <el-input v-model="temp.rfid"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel()">取 消</el-button>
        <!--<el-button v-if="dialogStatus=='create'" type="primary" @click="create">确 定</el-button>-->
        <el-button type="primary" @click="update">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import messageApi from '@/api/mx/message2.js'
export default {
  name: 'message2',
  data () {
    return {
      chooseList: [
        {label: 'device_imei', value: 'device_imei'},
        {label: 'device_rfid', value: 'device_rfid'}
      ], // 选择查询条件
      chooseName: '请输入', // 输入框显示提示
      queryType: 'device_imei', // 默认查询条件
      queryValue: '', // 输入框 输入的值
      dialogStatus: '',
      dialogFormVisible: false, // 弹出界面默认关闭
      textMap: {
        update: '编辑',
        create: '创建'
      }, // 弹出界面根据输入值，显示标题
      temp: {
        imei: '',
        rfid: ''
      },
      listQuery: {
        imei: ''
      },
      list: null, // 从接口获取数据显示的列表
      total: null, // 从接口获取数据的总数
      pageSizes: [5, 10, 20, 30, 50] // 设置每页显示的数据条数，可修改
    }
  },
  created () {
    this.getList()
  },
  methods: {
    getList () {
      if (this.queryValue) {
        this.listQuery.imei = this.queryValue
        messageApi.getRecord(this.listQuery).then(res => {
          this.list = res.data.result.info
          this.total = res.data.result.count
          console.log(res)
        })
      } else {
        messageApi.getRecord().then(res => {
          this.list = res.data.result.info
          this.total = res.data.result.count
          console.log(res)
        })
      }
    }, // 测试axios连接接口
    getList1 () {
      messageApi.getRecord().then(res => {
        this.list = res.data.result.info
        this.total = res.data.result.count
        console.log(res)
      })
    }, // 获取所有数据
    handleUpdate (row) {
      this.temp.imei = row.device_imei
      this.temp.rfid = row.device_rfid
      this.dialogStatus = 'update'
      this.dialogFormVisible = true
    },
    handleDelete (row) {
      this.$confirm('此操作将永久删除该条记录, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.listQuery.imei = row.device_imei
        messageApi.deleteRecord(this.listQuery).then(res => {
          this.getList()
          this.$notify({
            title: '成功',
            message: '删除成功',
            type: 'success',
            duration: 2000
          })
        })
      })
    },
    update () {
      this.$refs.temp.validate((valid) => {
        if (valid) {
          messageApi.updateRecord(this.temp).then(res => {
            this.getList()
            this.dialogFormVisible = false
            this.$notify({
              title: '成功',
              message: '更新成功',
              type: 'success',
              duration: 2000
            })
            this.$refs.temp.resetFields()
          })
        } else {
          return false
        }
      })
    },
    handleadd () {
      this.temp.imei = null
      this.temp.rfid = null
      this.dialogStatus = 'create'
      this.dialogFormVisible = true
    },
    cancel () {
      this.dialogFormVisible = false
    },
    handleSizeChange (val) {
      this.listQuery.limit = val
      // this.getList()
      console.log(`每页 ${val} 条`)
    },
    handleCurrentChange (val) {
      this.listQuery.offset = (val - 1) * this.listQuery.limit
      // this.handleQuery()
      console.log(`当前页: ${val}`)
    }
  }
}
</script>
<style scoped>

</style>



