import fetch from '@/utils/fetch'

const api = {
  getRecord (query) {
    return fetch({
      url: '/api/rf_demo/deviceImeiInfo/query',
      method: 'get',
      params: query
    })
  }, // /v1/api/rf_demo/deviceImeiInfo/query  --用于查询IMEI和RFID对应关系 ，参数IMEI(可选,默认查询所有)
  deleteRecord (query) {
    return fetch({
      url: '/api/rf_demo/deviceImeiInfo/delete',
      method: 'delete',
      params: query
    })
  }, // /v1/api/rf_demo/deviceImeiInfo/delete --用于删除IMEI对应的记录，参数imei
  updateRecord (query) {
    return fetch({
      url: '/api/rf_demo/deviceImeiInfo/update',
      method: 'post',
      params: query
    })
  }// /v1/api/rf_demo/deviceImeiInfo/update   --- 用于新增和更新 IMEI和RFID的对应关系，参数rfid和imei

}

export default api



